@font-face {
  font-family: "font-1";
  src: url(../assets/fonts/JosefinSans-Medium.ttf);
}

$font-1: "font-1", sans-serif;

$color-1: #b3b5fd;
$color-2: rgb(199, 151, 30);
$color-3: #1d1f24;
$white: rgb(243, 243, 243);
$black: rgb(41, 39, 39);


@mixin verticalCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

body {
  background: $black;
  font-family: $font-1;
  // @include verticalCenter;
  color: beige;

}

li {
  list-style-type: none;
}

p {
  margin: 100px, 100px;
  width: 50vw;
  margin-left: 12em;
}